<template>
  <div id="app">
    <nav id="menu">
      <a href="#work" @click.stop.prevent="navClicked">Work</a>
      <a href="#skills" @click.stop.prevent="navClicked">Skill</a>
      <a href="#contact" @click.stop.prevent="navClicked">Contact</a>
      <a
        href="https://drive.google.com/file/d/1x0tgZUXG8ieqp1NkGfCZEOrjs1Y6vrEd/view?usp=sharing"
        target="_blank"
      >Resume</a>
    </nav>
    <router-view></router-view>
    <a src="#" @click.stop.prevent="toggleMenu" class="hamburg"></a>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      menushown: false
    };
  },
  mounted() {
    // Toogle menu showing on body click
    document.body.addEventListener("click", () => {
      if (this.menushown) {
        this.toggleMenu();
      }
    });
  },
  methods: {
    toggleMenu(event) {
      const $hamburgLink = document.querySelector(".hamburg");
      const $body = document.body;

      document.body.classList.toggle("tilt");
      this.menushown = !this.menushown;
      if (this.menushown) {
        if (
          $hamburgLink.classList.contains("black") &&
          $body.classList.contains("tilt")
        ) {
          $hamburgLink.classList.remove("black");
        }
        setTimeout(function() {
          document.body.classList.add("tiltz");
        }, 350);
      } else {
        if (!$hamburgLink.classList.contains("black")) {
          $hamburgLink.classList.add("black");
        }
        document.body.classList.remove("tiltz");
      }
    },
    navClicked(e) {
      let scrollTo = e.target.hash;

      function srcollToElem(element, to, duration) {
        if (duration <= 0) return;
        var difference = to - element.scrollTop;
        var perTick = (difference / duration) * 10;

        setTimeout(function() {
          element.scrollTop = element.scrollTop + perTick;
          if (element.scrollTop === to) return;
          srcollToElem(element, to, duration - 10);
        }, 10);
      }

      this.toggleMenu();

      const fromElement = document.querySelector("#body");
      let toElement = document.querySelector(scrollTo);

      setTimeout(() => {
        srcollToElem(fromElement, toElement.offsetTop, 1000);
      }, 500);
    }
  }
};
</script>

<style>
html,
body {
  background: #181818;
  overflow: hidden;
  position: relative;
}
body {
  font-family: "Poppins", sans-serif !important;
}
#menu {
  position: absolute;
  top: 70px;
  left: 20px;
}
#menu a {
  color: #fff;
  display: block;
  margin-bottom: 15px;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.8);
  outline: none;
}
#menu a:hover {
  color: #fff;
}
.tilt #menu {
  z-index: 1001;
}
.hamburg {
  position: absolute;
  z-index: 1000;
  top: 22px;
  left: 20px;
  border-top: 2px solid #fff;
  width: 20px;
  height: 20px;
  cursor: pointer;
  transition: border 500ms ease-in-out;
}
.hamburg:before {
  content: "";
  position: absolute;
  top: 12px;
  border-top: 2px solid #fff;
  width: 20px;
  transition: border 500ms ease-in-out;
}
.hamburg:after {
  content: "";
  position: absolute;
  top: 5px;
  border-top: 2px solid #fff;
  width: 20px;
  transition: border 500ms ease-in-out;
}
.hamburg.black {
  border-top: 2px solid #000;
}
.hamburg.black:before {
  border-top: 2px solid #000;
}
.hamburg.black:after {
  border-top: 2px solid #000;
}
</style>
